import React, { useEffect, useState } from "react";
import {
    Card,
    Container,
    Row,
    Col,
    Media,
    ProgressBar
  } from "react-bootstrap";
  import { Link } from 'react-router-dom';
  import { AiOutlineCalendar } from 'react-icons/ai';
  import { BiNotification } from 'react-icons/bi';
  import { AiOutlineTeam } from 'react-icons/ai';
  import { GrTemplate } from 'react-icons/gr';
  import { CreateNewTemplate } from './../Comps/Modals/CreateNewTemplate.jsx';
  import { CreateMeeting } from './../Comps/Modals/CreateMeeting.jsx'

  export function ManagerDashboard(props) {

    const { uuid, num_meetings, num_templates, num_trainees } = props;
    const [templates, setTemplates ] = useState({});
    const [ events, setEvents ] = useState({});
    const [ trainees, setTrainees ] = useState({});
    const [ modalTemplateShow, setModalTemplateShow ] = useState(false);
    const [ modalMeetingShow, setModalMeetingShow ] = useState(false);

    useEffect(() => {
        fetch('/https://train-backend-api.herokuapp.com/api/manager/get_trainees', {
          method: 'POST',
          mode: 'cors',
          headers: {
              'Content-Type': 'application/json',
              'Access-Control-Allow-Origin': 'http://localhost:3000'
          },
          body: JSON.stringify({
              uuid: uuid
          })
      }).then(response => response.json()).then(json => {
          setTrainees({
              trainee_names: json.trainee_names,
              trainee_emails: json.trainee_emails,
              trainee_uuids: json.trainee_uuids
          });
      }).catch(error => {
          console.log(error);
      });
      fetch('/https://train-backend-api.herokuapp.com/api/manager/get_training_templates', {
            method: 'POST',
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': 'http://localhost:3000'
            },
            body: JSON.stringify({
                uuid: uuid
            })
        }).then(response => response.json()).then(json => {
            setTemplates({
                template_names: json.template_names,
                template_ids: json.template_uuids,
                descriptions: json.descriptions
            });
        }).catch(error => {
            console.log(error);
        });

        fetch('/https://train-backend-api.herokuapp.com/manager/get_events', {
            method: 'POST',
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': 'http://localhost:3000'
            },
            body: JSON.stringify({
                uuid: uuid
            })
        }).then(response => response.json()).then(json => {
            setEvents({
                meetings: json.meetings
          });
        }).catch(error => {
            console.log(error);
        });
    }, [uuid]);
    
    useEffect(() => {
      props.onBrandChange("Dashboard");
    });

    const template_names = Object.assign([], templates.template_names);
    const meetings = Object.assign([], events.meetings);
    
      return (
        <React.Fragment>
          <Container fluid>
            <Row>
              <Col lg="3" sm="6">
                <Card className="card-stats">
                  <Card.Body>
                    <Row>
                      <Col xs="5">
                        <div className="icon-big text-center icon-warning">
                          <AiOutlineCalendar color="gold"/>
                        </div>
                      </Col>
                      <Col xs="7">
                        <div className="numbers">
                          <p className="card-category">Upcoming Meetings</p>
                          <Card.Title as="h4">{ num_meetings }</Card.Title>
                        </div>
                      </Col>
                    </Row>
                  </Card.Body>
                  <Card.Footer>
                    <hr></hr>
                    <div className="stats">
                      <Link className="text-muted" style={{ color: "light" }} to='/user/calendar'>
                        View All Meetings and Events
                      </Link>
                    </div>
                  </Card.Footer>
                </Card>
              </Col>
              <Col lg="3" sm="6">
                <Card className="card-stats">
                  <Card.Body>
                    <Row>
                      <Col xs="5">
                        <div className="icon-big text-center icon-warning">
                          <BiNotification color="green" />
                        </div>
                      </Col>
                      <Col xs="7">
                        <div className="numbers">
                          <p className="card-category">Notifications</p>
                          <Card.Title as="h4">0</Card.Title>
                        </div>
                      </Col>
                    </Row>
                  </Card.Body>
                  <Card.Footer>
                    <hr></hr>
                    <div className="stats">
                      <i className="far fa-calendar-alt mr-1"></i>
                      No New Notifications
                    </div>
                  </Card.Footer>
                </Card>
              </Col>
              <Col lg="3" sm="6">
                <Card className="card-stats">
                  <Card.Body>
                    <Row>
                      <Col xs="5">
                        <div className="icon-big text-center icon-warning">
                          <AiOutlineTeam color="red" />
                        </div>
                      </Col>
                      <Col xs="7">
                        <div className="numbers">
                          <p className="card-category">My Trainees</p>
                          <Card.Title as="h4">{ num_trainees }</Card.Title>
                        </div>
                      </Col>
                    </Row>
                  </Card.Body>
                  <Card.Footer>
                    <hr></hr>
                    <div className="stats">
                      <i className="far fa-clock-o mr-1"></i>
                      <Link className="text-muted" to="/user/trainees">View Trainees</Link>
                    </div>
                  </Card.Footer>
                </Card>
              </Col>
              <Col lg="3" sm="6">
                <Card className="card-stats">
                  <Card.Body>
                    <Row>
                      <Col xs="5">
                        <div className="icon-big text-center icon-warning">
                          <GrTemplate color="red" />
                        </div>
                      </Col>
                      <Col xs="7">
                        <div className="numbers">
                          <p className="card-category">Total Templates</p>
                          <Card.Title as="h4">{ num_templates }</Card.Title>
                        </div>
                      </Col>
                    </Row>
                  </Card.Body>
                  <Card.Footer>
                    <hr></hr>
                    <div className="stats">
                      <Link className="text-muted" to="#">View Templates</Link>
                    </div>
                  </Card.Footer>
                </Card>
              </Col>
            </Row>
            <Row>
              <Col md="8">
                <Card>
                  <Card.Header>
                    <Card.Title as="h4" className="text-muted">My Templates</Card.Title>
                  </Card.Header>
                  <Card.Body>
                    <ul className="list-unstyled">
                      { template_names.map((template_name,idx) => 
                        <Card key={ idx }>
                          <Card.Body>
                            <Media as="li">
                              <img
                                width={64}
                                height={64}
                                className="mr-3"
                                src={require("./../Assets/img/" + template_name.substr(0, template_name.indexOf(" ")).toLowerCase() + ".png").default}
                                alt={ require("./../Assets/img/user.png") }
                              ></img>
                              <Media.Body>
                                <Link className="text-muted" to="#"><h5>{ template_name }</h5></Link>
                                <p>
                                  { templates.descriptions[idx] }
                                </p>
                              </Media.Body>
                            </Media>
                          </Card.Body>
                        </Card>
                      )}
                    </ul>
                  </Card.Body>
                  <Card.Footer>
                    <hr></hr>
                    <div className="stats">
                      <Link className="text-muted" to="#" onClick={ () => setModalTemplateShow(true) }>Create New Template +</Link>
                    </div>
                  </Card.Footer>
                </Card>
              </Col>
              <Col md="h4">
                <Card>
                  <Card.Header>
                    <Card.Title as="h4" className="text-muted">Upcoming Mettings</Card.Title>
                    <p className="card-category">Remember to prepare accordingly</p>
                  </Card.Header>
                  <Card.Body>
                        <ul className="list-unstyled">
                          { meetings.map((meeting, idx) => 
                            <Card key={ idx }>
                              <Card.Body>
                                <Media as="li">
                                  <img
                                    width={64}
                                    height={64}
                                    className="mr-3 avatar"
                                    src={require("./../Assets/img/" + meeting.with.substr(0, meeting.with.indexOf(" ")).toLowerCase() + ".jpg").default}
                                    alt={require("./../Assets/img/user.png").default}
                                  ></img>
                                  <Media.Body>
                                    <h6 className="text-muted">{ meeting.with }</h6>
                                    <br />
                                    <p>Date: { meeting.start.substr(0, meeting.start.indexOf("T"))}</p>
                                    <p>Time: { meeting.start.substr(meeting.start.indexOf("T") + 1 , meeting.start.length - 1)}</p>
                                  </Media.Body>
                                </Media>
                              </Card.Body>
                            </Card>
                          )}
                        </ul>
                  </Card.Body>
                  <Card.Footer>
                    <hr></hr>
                    <div className="stats">
                      <Link className="text-muted" to="#" onClick={ () => setModalMeetingShow(true) }>Create New Meeting +</Link>
                    </div>
                  </Card.Footer>
                </Card>
              </Col>
            </Row>
              <CreateNewTemplate
                show={ modalTemplateShow }
                onHide={ () => setModalTemplateShow(false) }
                uuid = { uuid }
              />
              <CreateMeeting
                show={ modalMeetingShow }
                onHide={ () => setModalMeetingShow(false) }
                uuid={ uuid }
                trainees={ trainees }
              />
          </Container>
        </React.Fragment>
      )
  }
