import React from 'react';
import { Form, Button, Alert, Col, Container } from 'react-bootstrap';
import { Redirect, Link } from 'react-router-dom';


export class Signup extends React.Component {
    state = {
        first_name: "",
        last_name: "",
        email: "",
        age: "",
        address: "",
        password: "",
        confirm_pass: "",
        redirect: false,
        validated: false,
        confirmPassAlert: false,
        exisitngUserAlert: false
    }

    setRedirect = (b) => {
        this.setState({
            redirect: b
        })
    }

    renderRedirect = () => {
        if (this.state.redirect) {
            return <Redirect to="/user/dashboard" />
        }
    }

    setExistingUserAlert = () => {
        this.setState({
            existingUserAlert: true
        })
    }

    setConfrimPassAlert = () => {
        this.setState({
            confirmPassAlert: true
        })
    }

    renderExistingUserAlert = () => {
        if (this.state.existingUserAlert) {
            return <Alert variant="danger">User already exists<Alert.Link href="/login"> Try Loggin In</Alert.Link></Alert>
        }
    }

    renderConfirmPassAlert = () => {
        if (this.state.confrimPassAlert) {
            return <Alert variant="danger">Passwords do no match</Alert>
        }
    }

    handleSignup = e => {
        const form = e.currentTarget;
        if (form.checkValidity() === false) {
            e.preventDefault();
            e.stopPropagation();
        } else {
            e.preventDefault();
            fetch('/https://train-backend-api.herokuapp.com/api/signup', {
                method: 'POST',
                mode: 'cors',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    first_name: this.state.first_name,
                    last_name: this.state.last_name,
                    email: this.state.email,
                    age: this.state.age,
                    address: this.state.address,
                    password: this.state.password,
                    confirm_pass: this.state.confirm_pass
                })
            }).then(response => response.json()).then(json => {
                this.props.onSignup(json);
                this.setRedirect(json.isLoggedIn);
                if (json.msg === 'Password and confirm password do not match.') {
                    this.setConfrimPassAlert();
                }
                if (json.msg === 'User already exists.') {
                    this.setExistingUserAlert();
                }
            }).catch(error => {
                console.log(error);
                this.props.onSignupError(error);
                this.setRedirect(false);
            });
        }

        this.setState({
            validated: true
        })
        
    };

    handleFirstNameChange = e => {
        this.setState({
            first_name: e.target.value
        });
    };

    handleLastNameChange = e => {
        this.setState({
            last_name: e.target.value
        });
    };

    handleEmailChange = e => {
        this.setState({
            email: e.target.value
        });
    };

    handleAgeChange = e => {
        this.setState({
            age: e.target.value
        });
    };

    handleAddressChange = e => {
        this.setState({
            address: e.target.value
        });
    };

    handlePasswordChange = e => {
        this.setState({
            password: e.target.value
        });
    };

    handleConfirmPassChange = e => {
        this.setState({
            confirm_pass: e.target.value
        });
    };

    render() {
        return (
            <Container style={{ marginTop: 50 }}>
                <Form noValidate validated={ this.state.validated }>
                    { this.renderConfirmPassAlert() }
                    { this.renderExistingUserAlert() }
                    <Form.Row>
                        <Form.Group as={Col} size="lg" controlId="firstName">
                            <Form.Label>First Name</Form.Label>
                            <Form.Control
                                required
                                type="text"
                                placeholder="First Name"
                                value={this.state.first_name}
                                onChange={ this.handleFirstNameChange }
                            ></Form.Control>
                            <Form.Control.Feedback type="invalid">
                                First Name is Required
                            </Form.Control.Feedback>
                        </Form.Group>

                        <Form.Group as={Col} size="lg" controlId="lastName">
                            <Form.Label>Last Name</Form.Label>
                            <Form.Control
                                required
                                type="text"
                                placeholder="Last Name"
                                name="last_name"
                                value={ this.state.last_name }
                                onChange={ this.handleLastNameChange }
                            ></Form.Control>
                            <Form.Control.Feedback type="invalid">
                                Last Name is Required
                            </Form.Control.Feedback>
                        </Form.Group>
                    </Form.Row>

                    <Form.Row>
                        <Form.Group as={Col} size="lg" controlId="email">
                            <Form.Label>Email</Form.Label>
                            <Form.Control
                                required
                                type="email"
                                placeholder="Email Address"
                                name="email"
                                value={ this.state.email }
                                onChange={ this.handleEmailChange }
                            ></Form.Control>
                            <Form.Control.Feedback type="invalid">
                                Email is Required
                            </Form.Control.Feedback>
                            <Form.Text className="text-muted">
                                We'll never share your email with anyone.
                            </Form.Text>
                        </Form.Group>

                        <Form.Group as={Col} size="lg" controlId="age">
                            <Form.Label>Age</Form.Label>
                            <Form.Control
                                required
                                type="text"
                                placeholder="Age"
                                name="age"
                                value={ this.state.age }
                                onChange={ this.handleAgeChange }
                            ></Form.Control>
                            <Form.Control.Feedback type="invalid">
                                Age is Required
                            </Form.Control.Feedback>
                        </Form.Group>
                    </Form.Row>

                    <Form.Group size="lg" controlId="address">
                        <Form.Label>Address</Form.Label>
                        <Form.Control
                            required
                            type="text"
                            placeholder="Address"
                            name="address"
                            value={ this.state.address }
                            onChange={ this.handleAddressChange }
                        ></Form.Control>
                        <Form.Control.Feedback type="invalid">
                            Address is Required
                        </Form.Control.Feedback>
                    </Form.Group>

                    <Form.Row>
                        <Form.Group as={Col} size="lg" controlId="password">
                            <Form.Label>Password</Form.Label>
                            <Form.Control
                                required
                                type="password"
                                placeholder="Password"
                                name="password"
                                value={ this.state.password }
                                onChange={ this.handlePasswordChange }
                            ></Form.Control>
                            <Form.Control.Feedback type="invalid">
                                Password is Required
                            </Form.Control.Feedback>
                        </Form.Group>

                        <Form.Group as={Col} size="lg" controlId="confirmPassword">
                            <Form.Label>Confirm Password</Form.Label>
                            <Form.Control
                                required
                                type="password"
                                placeholder="Confirm Password"
                                name="confirm_pass"
                                value={ this.state.confirm_pass }
                                onChange={ this.handleConfirmPassChange }
                            ></Form.Control>
                            <Form.Control.Feedback type="invalid">
                                Confirm Password is Required
                            </Form.Control.Feedback>
                        </Form.Group>
                    </Form.Row>

                    { this.renderRedirect() }
                    <Button block size="lg" type="submit" variant="primary" onClick={ this.handleSignup }>
                        Signup
                    </Button>
                    <Link to="/login" className="text-muted">Already have an account?</Link>
                </Form>
            </Container>
        )
    }
}