import React, { useState } from 'react';
import {
    Modal,
    Form,
    Col,
    Card,
    Button,
    Alert
} from 'react-bootstrap';

export function CreateNewTemplate(props) {
    const { uuid } = props;
    const [ validated, setValidated ] = useState(false);
    const [ templateName, setTemplateName ] = useState('');
    const [ technology, setTechnology ] = useState('');
    const [ description, setDescription ] = useState('');
    const [ file, setFile ] = useState('');
    const [ msg, setMsg ] = useState('');
    const [ alert, setAlert ] = useState(false);
    const [ success, setSuccess ] = useState(false);

    const handleTemplateNameChange = (e) => {
        setTemplateName(e.target.value);
    }

    const handleTechnologyChange = (e) => {
        setTechnology(e.target.value);
    }

    const handleDescriptionChange = (e) => {
        setDescription(e.target.value);
    }

    const handleFileChange = (e) => {
        setFile(e.target.value);
    }

    const renderAlert = (msg) => {
        if (alert) {
            return (
                <Alert variant="danger">{ msg }</Alert>
            )
        }
    }

    const renderSuccess = (msg) => {
        if (success) {
            return (
                <Alert variant="success">{ msg }</Alert>
            )
        }
    }

    function refreshPage() {
        window.location.reload(false);
    }

    const handleSubmit = (e) => {
        const form = e.currentTarget;
        if (form.checkValidity() === false) {
            e.preventDefault();
            e.stopPropagation();
        } else {
            e.preventDefault();
            fetch('/https://train-backend-api.herokuapp.com/api/manager/create_template', {
                method: 'POST',
                mode: 'cors',
                headers: {
                    'Content-Type': 'application/json',
                    'Access-Control-Allow-Origin': 'http://localhost:3000'
                },
                body: JSON.stringify({
                    uuid: uuid,
                    template_name: templateName,
                    technology: technology,
                    description: description,
                    file: file
                })
            }).then(response => response.json()).then(json => {
                console.log(json);
                if (json.success === false) {
                    setAlert(true);
                    setMsg(json.msg);
                } 
                if (json.success === true) {
                    setSuccess(true);
                    setMsg(json.msg);
                    refreshPage();
                }
            }).catch(error => {
                console.log(error);
                setAlert(true);
                setMsg(error.msg);
            });
        }

        setValidated(true);
    }

    return (
        <Modal 
            { ...props }
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title id="contained-modal-title-vcenter" className="text-muted">
                    Create New Template
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Card>
                    { renderAlert(msg) }
                    { renderSuccess(msg) }
                    <Form noValidate validated={ validated } style={{ marginTop: 50, marginBottom: 50, marginRight: 50, marginLeft: 50 }}>
                        <Form.Row>
                            <Form.Group as={ Col } md="6">
                                <Form.Label>Template Name</Form.Label>
                                <Form.Control
                                    required
                                    type="text"
                                    placeholder="Enter Template Name"
                                    onChange={ handleTemplateNameChange }
                                ></Form.Control>
                                <Form.Control.Feedback type="invalid">Template Name is required</Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group as={ Col } md="3">
                                <Form.Label>Choose Technology</Form.Label>
                                <Form.Control
                                    required
                                    as="select"
                                    value={ technology }
                                    onChange={ handleTechnologyChange }
                                >
                                    <option>Chooose...</option>
                                    <option value="Angular">Angular</option>
                                    <option value="NodeJS">NodeJS</option>
                                </Form.Control>
                                <Form.Control.Feedback type="invalid">Technology is required</Form.Control.Feedback>
                            </Form.Group>
                        </Form.Row>
                        <Form.Group>
                            <Form.Label>Template Description</Form.Label>
                            <Form.Control
                                required
                                as="textarea"
                                rows={ 4 }
                                onChange={ handleDescriptionChange }
                            />
                            <Form.Control.Feedback>Description is required</Form.Control.Feedback>
                        </Form.Group>
                        <Form.Group>
                            <Form.File id="aditionalTemplateFiles" label="Aditional Files" onChange={ handleFileChange }/>
                        </Form.Group>
                        <Button variant="primary" type="submit" onClick={ handleSubmit }>
                            Submit
                        </Button>
                    </Form>
                </Card>
            </Modal.Body>
        </Modal>
    )
}