import React, { useEffect, useState } from "react";
import {
    Card,
    Container,
    Row,
    Col,
    Media,
    ProgressBar
  } from "react-bootstrap";
  import { Link } from 'react-router-dom';
  import { AiOutlineCalendar } from 'react-icons/ai';
  import { BiNotification } from 'react-icons/bi';
  import { AiOutlineTeam } from 'react-icons/ai';
  import { GrTemplate } from 'react-icons/gr';

  export function TraineeDashboard(props) {

    const { uuid, num_meetings, num_teammates, num_templates } = props;
    const [ trainings, setTrainings ] = useState({});
    const [ events, setEvents ] = useState({});

    useEffect(() => {
      fetch('/https://train-backend-api.herokuapp.com/api/trainee/trainings', {
            method: 'POST',
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': 'http://localhost:3000'
            },
            body: JSON.stringify({
                uuid: uuid
            })
        }).then(response => response.json()).then(json => {
            setTrainings({
                training_ids: json.template_ids,
                training_names: json.template_names,
                descriptions: json.descriptions
            });
        }).catch(error => {
            console.log(error);
        });

        fetch('/https://train-backend-api.herokuapp.com/api/trainee/get_events', {
            method: 'POST',
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': 'http://localhost:3000'
            },
            body: JSON.stringify({
                uuid: uuid
            })
        }).then(response => response.json()).then(json => {
            setEvents({
                meetings: json.events
          });
        }).catch(error => {
            console.log(error);
        });
    }, [uuid]);

    useEffect(() => {
      props.onBrandChange("Dashboard");
    });
    
    const training_names = Object.assign([], trainings.training_names);
    const meetings = Object.assign([], events.meetings);

    console.log(meetings);
    
      return (
        <React.Fragment>
          <Container fluid>
            <Row>
              <Col lg="3" sm="6">
                <Card className="card-stats">
                  <Card.Body>
                    <Row>
                      <Col xs="5">
                        <div className="icon-big text-center icon-warning">
                          <AiOutlineCalendar color="gold"/>
                        </div>
                      </Col>
                      <Col xs="7">
                        <div className="numbers">
                          <p className="card-category">Upcoming Meetings</p>
                          <Card.Title as="h4">{ num_meetings }</Card.Title>
                        </div>
                      </Col>
                    </Row>
                  </Card.Body>
                  <Card.Footer>
                    <hr></hr>
                    <div className="stats">
                      <Link className="text-muted" style={{ color: "light" }} to='/user/calendar'>
                        View All Meetings and Events
                      </Link>
                    </div>
                  </Card.Footer>
                </Card>
              </Col>
              <Col lg="3" sm="6">
                <Card className="card-stats">
                  <Card.Body>
                    <Row>
                      <Col xs="5">
                        <div className="icon-big text-center icon-warning">
                          <BiNotification color="green" />
                        </div>
                      </Col>
                      <Col xs="7">
                        <div className="numbers">
                          <p className="card-category">Notifications</p>
                          <Card.Title as="h4">0</Card.Title>
                        </div>
                      </Col>
                    </Row>
                  </Card.Body>
                  <Card.Footer>
                    <hr></hr>
                    <div className="stats">
                      <i className="far fa-calendar-alt mr-1"></i>
                      No New Notifications
                    </div>
                  </Card.Footer>
                </Card>
              </Col>
              <Col lg="3" sm="6">
                <Card className="card-stats">
                  <Card.Body>
                    <Row>
                      <Col xs="5">
                        <div className="icon-big text-center icon-warning">
                          <AiOutlineTeam color="red" />
                        </div>
                      </Col>
                      <Col xs="7">
                        <div className="numbers">
                          <p className="card-category">My Team</p>
                          <Card.Title as="h4">{ num_teammates }</Card.Title>
                        </div>
                      </Col>
                    </Row>
                  </Card.Body>
                  <Card.Footer>
                    <hr></hr>
                    <div className="stats">
                      <i className="far fa-clock-o mr-1"></i>
                      <Link className="text-muted" to="/user/team">View Team</Link>
                    </div>
                  </Card.Footer>
                </Card>
              </Col>
              <Col lg="3" sm="6">
                <Card className="card-stats">
                  <Card.Body>
                    <Row>
                      <Col xs="5">
                        <div className="icon-big text-center icon-warning">
                          <GrTemplate color="red" />
                        </div>
                      </Col>
                      <Col xs="7">
                        <div className="numbers">
                          <p className="card-category">Trainings in Progress</p>
                          <Card.Title as="h4">{ num_templates }</Card.Title>
                        </div>
                      </Col>
                    </Row>
                  </Card.Body>
                  <Card.Footer>
                    <hr></hr>
                    <div className="stats">
                      <Link className="text-muted" to="#">View Trainings</Link>
                    </div>
                  </Card.Footer>
                </Card>
              </Col>
            </Row>
            <Row>
              <Col md="8">
                <Card>
                  <Card.Header>
                    <Card.Title as="h4" className="text-muted">Current Trainings</Card.Title>
                  </Card.Header>
                  <Card.Body>
                    <ul className="list-unstyled">
                      { training_names.map((training_name,idx) => 
                        <Card key={ idx }>
                          <Card.Body>
                            <Media as="li">
                              <img
                                width={64}
                                height={64}
                                className="mr-3"
                                src={require("./../Assets/img/" + training_name.substr(0, training_name.indexOf(" ")).toLowerCase() + ".png").default}
                                alt="..."
                              ></img>
                              <Media.Body>
                                <Link className="text-muted" to="#"><h5>{ training_name }</h5></Link>
                                <p>
                                  { trainings.descriptions[idx] }
                                </p>
                                <ProgressBar now={60} label={"60%"} />
                              </Media.Body>
                            </Media>
                          </Card.Body>
                        </Card>
                      )}
                    </ul>
                  </Card.Body>
                  <Card.Footer>
                    <hr></hr>
                    <div className="stats">
                      <i className="fas fa-history"></i>
                      Latest Progress 1 Day(s) Ago
                    </div>
                  </Card.Footer>
                </Card>
              </Col>
              <Col md="h4">
                <Card>
                  <Card.Header>
                    <Card.Title as="h4" className="text-muted">Upcoming Mettings</Card.Title>
                    <p className="card-category">Remember to prepare accordingly</p>
                  </Card.Header>
                  <Card.Body>
                        <ul className="list-unstyled">
                          { meetings.map((meeting, idx) => 
                            <Card key={ idx }>
                              <Card.Body>
                                <Media as="li">
                                  <img
                                    width={64}
                                    height={64}
                                    className="mr-3 avatar"
                                    src={require("./../Assets/img/" + meeting.with.substr(0, meeting.with.indexOf(" ")).toLowerCase() + ".jpg").default}
                                    alt={require("./../Assets/img/user.png").default}
                                  ></img>
                                  <Media.Body>
                                    <h6 className="text-muted">{ meeting.with }</h6>
                                    <br />
                                    <p>Date: { meeting.start.substr(0, meeting.start.indexOf("T"))}</p>
                                    <p>Time: { meeting.start.substr(meeting.start.indexOf("T") + 1 , meeting.start.length - 1)}</p>
                                  </Media.Body>
                                </Media>
                              </Card.Body>
                            </Card>
                          )}
                        </ul>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Container>
        </React.Fragment>
      )
  }
