import React from 'react';
import {
    Button,
    Form,
    Alert
  } from "react-bootstrap";
import { Link, Redirect } from 'react-router-dom';

export class Login extends React.Component {
    state = {
        email: "",
        password: "",
        redirect: false,
        alert: false,
        validated: false
    };

    setRedirect = (b) => {
        this.setState({
            redirect: b
        })
    }

    renderRedirect = () => {
        if (this.state.redirect){
            return <Redirect to="/user/dashboard" />
        }
    }

    setAlert = () =>{
        this.setState({
            alert: true
        })
    }

    renderAlert = () => {
        if (this.state.alert) {
            return <Alert variant="danger">Invalide Email or Password</Alert>
        }
    }

    handleLogin = (e) => {
        const form = e.currentTarget;
        if (form.checkValidity() === false) {
            e.preventDefault();
            e.stopPropagation();
        } else {

            e.preventDefault()
            console.log(this.state.email);
            fetch('/https://train-backend-api.herokuapp.com/api/login', {
                method: 'POST',
                mode: 'cors',
                headers: {
                    'Content-Type': 'application/json',
                    'Access-Control-Allow-Origin': 'http://localhost:3000'
                },
                body: JSON.stringify({
                    email: this.state.email,
                    password: this.state.password
                })
            }).then(response => response.json()).then(json => {
                console.log(json.isLoggedIn);
                this.props.onLogin(json);
                this.setRedirect(json.isLoggedIn);
            }).catch(error => {
                this.props.onLoginError(error);
                this.setRedirect(false);
                this.setAlert();
            });

        }

        this.setState({
            validated: true
        })
                
    };

    handleEmailChange = e => {
        this.setState({
            email: e.target.value
        });
    };

    handlePasswordChange = e => {
        this.setState({
            password: e.target.value
        });
    };

    render() {

        return (
            <div className="Login">
                <Form noValidate validated={ this.state.validated }>
                    { this.renderAlert() }
                    <Form.Group size="lg" controlId="email">
                        <Form.Label>Email</Form.Label>
                        <Form.Control
                            required
                            type="email"
                            placeholder="Email"
                            value={this.state.email}
                            onChange={this.handleEmailChange}
                        ></Form.Control>
                        <Form.Control.Feedback type="invalid">
                            Please Enter Your Email
                        </Form.Control.Feedback>
                    </Form.Group>
                    <Form.Group size="lg" controlId="password">
                        <Form.Label>Password</Form.Label>
                        <Form.Control
                            required
                            type="password"
                            value={this.state.password}
                            placeholder="Password"
                            onChange={this.handlePasswordChange}
                        ></Form.Control>
                        <Form.Control.Feedback type="invalid">
                            Please Enter Your Password
                        </Form.Control.Feedback>
                    </Form.Group>
                    { this.renderRedirect() }
                    <Button block size="lg" type="submit" variant="primary" onClick={ this.handleLogin }>
                        Login
                    </Button>
                </Form>
            </div>
        );
    }
}