import React, { useEffect, useState } from 'react';
import { ManagerSidebar } from "./../Comps/Sidebars/ManagerSidebar.jsx";
import { TraineeNavbar } from "./../Comps/Navbars/TraineeNavbar.jsx";
import { ManagerDashboard } from "./../Views/ManagerDashboard.jsx";
import { User } from "./../Views/UserProfile.jsx";
import { ManagerCalendar } from "./../Views/ManagerCalendar.jsx";
import { Trainees } from "./../Views/Trainees.jsx";
import {
    Switch,
    Route
  } from "react-router-dom";

export function ManagerLayout (props) {
    const { name, onLogout, uuid, email, address, age, aboutMe } = props;

    const [brand, setBrand] = useState("");

    const [dataState, setData] = useState({});

    const onBrandChange = (brand) => {
        setBrand(brand);
    }

    useEffect(() => {
        fetch('/https://train-backend-api.herokuapp.com/api/manager/dashboard', {
            method: 'POST',
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': 'http://localhost:3000'
            },
            body: JSON.stringify({
                uuid: uuid
            })
        }).then(response => response.json()).then(json => {
            setData({
                num_meetings: json.num_meetings,
                num_templates: json.num_templates,
                num_trainees: json.num_trainees
            });
        }).catch(error => {
            console.log(error);
        });
    }, [uuid]);


    return (
        <React.Fragment>
            <div className="wrapper">
                <ManagerSidebar name={ name }/>
                <div className="main-panel">
                    <TraineeNavbar onLogout={ onLogout } brand={ brand }/>  
                    <div className="content">
                        <Switch>
                            <Route path="/user/dashboard">
                                <ManagerDashboard 
                                    onBrandChange={ onBrandChange } 
                                    uuid={ uuid }
                                    num_meetings={ dataState.num_meetings }
                                    num_templates={ dataState.num_templates }
                                    num_trainees={ dataState.num_trainees }
                                />
                            </Route>
                            <Route path="/user/user-profile">
                                <User 
                                    onBrandChange={ onBrandChange } 
                                    name={ name } 
                                    email={ email }
                                    address={ address }
                                    age={ age }
                                    aboutMe={ aboutMe }
                                />
                            </Route>
                            <Route path="/user/calendar">
                                <ManagerCalendar onBrandChange={ onBrandChange } uuid={ uuid }/>
                            </Route>
                            <Route path="/user/trainees">
                                <Trainees uuid={ uuid } onBrandChange={ onBrandChange } />
                            </Route>
                        </Switch>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
    
}