import React, { useEffect, useState } from 'react';
import { TraineeSidebar } from "./../Comps/Sidebars/TraineeSidebar.jsx";
import { TraineeNavbar } from "./../Comps/Navbars/TraineeNavbar.jsx";
import { TraineeDashboard } from "./../Views/TraineeDashboard.jsx";
import { User } from "./../Views/UserProfile.jsx";
import { Calendar } from "./../Views/Calendar.jsx";
import { Team } from "./../Views/Team.jsx";
import { TraineeTrainings } from "./../Views/TraineeTrainings.jsx";
import {
    Switch,
    Route
  } from "react-router-dom";

export function TraineeLayout (props) {
    const { name, onLogout, uuid, email, address, age, aboutMe } = props;

    const [brand, setBrand] = useState("");

    const [dataState, setData] = useState({});

    const onBrandChange = (brand) => {
        setBrand(brand);
    }

    useEffect(() => {
        fetch('/https://train-backend-api.herokuapp.com/api/trainee/dashboard', {
            method: 'POST',
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': 'http://localhost:3000'
            },
            body: JSON.stringify({
                uuid: uuid
            })
        }).then(response => response.json()).then(json => {
            setData({
                num_meetings: json.num_meetings,
                num_teammates: json.num_teammates,
                num_templates: json.num_templates
            });
        }).catch(error => {
            console.log(error);
        });
    }, [uuid]);

    return (
        <React.Fragment>
            <div className="wrapper">
                <TraineeSidebar name={ name }/>
                <div className="main-panel">
                    <TraineeNavbar onLogout={ onLogout } brand={ brand }/>  
                    <div className="content">
                        <Switch>
                            <Route path="/user/dashboard">
                                <TraineeDashboard 
                                    onBrandChange={ onBrandChange } 
                                    uuid={ uuid }
                                    num_meetings={ dataState.num_meetings }
                                    num_teammates={ dataState.num_teammates }
                                    num_templates={ dataState.num_templates }
                                />
                            </Route>
                            <Route path="/user/user-profile">
                                <User 
                                    onBrandChange={ onBrandChange } 
                                    name={ name } 
                                    email={ email }
                                    address={ address }
                                    age={ age }
                                    aboutMe={ aboutMe }
                                />
                            </Route>
                            <Route path="/user/calendar">
                                <Calendar onBrandChange={ onBrandChange } uuid={ uuid }/>
                            </Route>
                            <Route path="/user/team">
                                <Team uuid={ uuid } onBrandChange={ onBrandChange } />
                            </Route>
                            <Route path="/user/trainings">
                                <TraineeTrainings uuid={ uuid } onBrandChange={ onBrandChange }/>
                            </Route>
                        </Switch>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
    
}