import React, { useEffect, useState } from "react";
import {
    Card,
    CardDeck,
    Container,
    Row,
  } from "react-bootstrap";
  import { Link } from 'react-router-dom';

export function Trainees(props) {
    const [ traineeState, setTrainee ] = useState({});
    const { uuid, onBrandChange } = props;

    useEffect(() => {
        fetch('/https://train-backend-api.herokuapp.com/api/manager/get_trainees', {
            method: 'POST',
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': 'http://localhost:3000'
            },
            body: JSON.stringify({
                uuid: uuid
            })
        }).then(response => response.json()).then(json => {
            setTrainee({
                trainee_names: json.trainee_names,
                trainee_emails: json.trainee_emails,
                trainee_uuids: json.trainee_uuids
            });
        }).catch(error => {
            console.log(error);
        });
    }, [uuid]);

    useEffect(() => {
        onBrandChange("My Team");
    });

    const names = Object.assign([], traineeState.trainee_names);

    return (
        <React.Fragment>
            <Container>
                <div className="row justify-content-md-center"><h5 className="text-muted">Your Trainees</h5></div>
                <div className="row justify-content-md-center">
                    <hr></hr>
                    <Row>
                        <CardDeck>
                            { names.map((name, idx) =>
                                <Card key={idx} bg="light" style={{ width: '64rem' }}>
                                    <div className="row justify-content-md-center">
                                        <img 
                                            src={require("../Assets/img/" + names[idx].substr(0, names[idx].indexOf(" ")).toLowerCase() + ".jpg").default}
                                            alt={ require("../Assets/img/user.png").default }
                                            style={{height: "5rem", width: "5rem", marginTop: "1rem"}}
                                            className="rounded-circle"
                                        ></img>
                                    </div>
                                    <div className="row justify-content-md-center">
                                        <Card.Title style={{marginTop: "1rem"}}>{ name }</Card.Title>
                                    </div>
                                    <div className="row justify-content-md-center">
                                        <Card.Text style={{marginTop: "2rem"}}>{ traineeState.trainee_emails[idx] }</Card.Text>
                                    </div>
                                    <hr></hr>
                                    <Card.Footer>
                                        <Link className="text-muted" to="#">View Training Plan</Link>
                                    </Card.Footer>
                                </Card>
                            )}
                        </CardDeck>
                    </Row>
                </div>
            </Container>
        </React.Fragment>
    )
}