import React from 'react';
import { Navbar, Container, Nav } from "react-bootstrap";
import {
    Switch,
    Route
  } from "react-router-dom";
import { Login } from "../Authentication/login.jsx";
import { Signup } from "../Authentication/signup.jsx";

export function LoggedInHomeNavbar (props) {
    const { name } = props;
    return (
        <React.Fragment>
            <Navbar bg="light" expand="lg">
                <Container fluid>
                    <div className="d-flex justify-content-center align-items-center ml-2 ml-lg-0">
                        <Navbar.Brand
                            href="/user/dashboard"
                            className="mr-2"
                        >
                            { name }
                        </Navbar.Brand>
                    </div>
                    <Navbar.Collapse id="vasic-navbar-nav">
                        <Nav className="nav mr-auto">
                        </Nav>
                        <Nav className="m-0" navbar>
                            <Nav.Item>
                                <Nav.Link
                                    className="m-0"
                                    href="/user/dashboard"
                                >
                                    <span className="no-icon">Account</span>
                                </Nav.Link>
                            </Nav.Item>
                            <Nav.Item>
                                <Nav.Link
                                    className="m-0"
                                    href="/logout"
                                >
                                    <span className="no-icon">Log Out</span>
                                </Nav.Link>
                            </Nav.Item>
                        </Nav>
                    </Navbar.Collapse>    
                </Container>
            </Navbar>

            <Switch>
                <Route exact path="/"></Route>
                <Route path="/api/login">
                    <Login onLogin={ props.onLogin } onLoginError={ props.onLoginError }/>
                </Route>
                <Route path="/api/signup">
                    <Signup onSignup={ props.onSignup } onSignupError={ props.onSignupError }/>
                </Route>
            </Switch>
        </React.Fragment>
    )
}