import React, { useState, useEffect } from 'react';
import { Inject, ScheduleComponent, Day, Week, WorkWeek, Month, Agenda } from '@syncfusion/ej2-react-schedule';
import Moment from 'moment';


export function ManagerCalendar(props) {
    const { onBrandChange, uuid } = props;
    const [ eventState, setEvent ] = useState([]);

    Moment.locale('en');

    useEffect(() => {
        fetch('/https://train-backend-api.herokuapp.com/api/manager/get_events', {
            method: 'POST',
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': 'http://localhost:3000'
            },
            body: JSON.stringify({
                uuid: uuid
            })
        }).then(response => response.json()).then(json => {
            console.log(json.meetings);
            const meetings = Object.assign([], json.meetings);

            for (var idx=0; idx < meetings.length; idx++) {
                var start_date = meetings[idx].start;
                var end_date = meetings[idx].end;
                start_date = Moment(start_date).format('YYYY M DD HH mm');
                end_date = Moment(end_date).format("YYYY M DD HH mm");
                const StartTime = start_date.split(" ");
                const EndTime = end_date.split(" ");
                const Subject = meetings[idx].text;

                const meeting = {
                    id: idx,
                    Subject: Subject,
                    StartTime: new Date(parseInt(StartTime[0], 10), parseInt(StartTime[1], 10) - 1, parseInt(StartTime[2], 10), parseInt(StartTime[3], 10), parseInt(StartTime[4], 10)),
                    EndTime: new Date(parseInt(EndTime[0], 10), parseInt(EndTime[1], 10) - 1, parseInt(EndTime[2], 10), parseInt(EndTime[3], 10), parseInt(EndTime[4], 10)),
                    isAllDay: false
                }
                setEvent(eventState => eventState.concat(meeting));
            }
        }).catch(error => {
            console.log(error);
        });
        
    }, [uuid]);

    console.log(eventState);

    useEffect(() => {
        onBrandChange("Calendar");
    })

    return (
        <React.Fragment>
            <ScheduleComponent eventSettings={{dataSource: eventState}} readonly={ false }>
                <Inject services={[Day, Week, WorkWeek, Month, Agenda]} />
            </ScheduleComponent>
        </React.Fragment>
    )
}