import './App.css';
import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
  HashRouter
} from "react-router-dom";
import { Home } from './Comps/home.jsx';
import { TraineeLayout } from './Layouts/traineeLayout.jsx';
import { ManagerLayout } from './Layouts/managerLayout.jsx';
import { HomeNavbar } from "./Comps/Navbars/HomeNavbar.jsx";
import { LoggedInHomeNavbar } from "./Comps/Navbars/LoggedInHomeNavbar.jsx";
import "bootstrap/dist/css/bootstrap.min.css";
import { Login } from "./Comps/Authentication/login.jsx";
import { Signup } from "./Comps/Authentication/signup.jsx";

class App extends React.Component {

  constructor(props) {
      super(props);
      this.state = {
        isLoggedIn: false,
        access_token: '',
        uuid: '',
        name: '',
        email: '',
        address: '',
        age: '',
        aboutMe: '',
        redirect: false
      };
  }

  onLogin = (data) => {
    this.setState({ 
      isLoggedIn: data.isLoggedIn,
      access_token: data.access_token,
      uuid: data.uuid,
      name: data.name,
      email: data.email,
      address: data.address,
      age: data.age,
      aboutMe: data.about_me,
      designation: data.designation
     });

     localStorage.setItem("user_data", JSON.stringify(this.state));
  }

  onSignup = (data) => {
    this.setState({
      isLoggedIn: data.isLoggedIn,
      access_token: data.access_token,
      uuid: data.uuid,
      name: data.name,
      email: data.email
    });

    localStorage.setItem("user_data", JSON.stringify(this.state));
  }

  onSignupError = (error) => {
    console.log(error);
    this.setState({ isLoggedIn: false});
  }

  onLogout = () => {
    localStorage.removeItem("user_data");
    this.setState({ isLoggedIn: false });
  }

  onLoginError = (error) => {
      console.log(error)
      this.setState({ isLoggedIn: false })
  }

  componentDidMount() {
    if (localStorage.getItem("user_data")) {
      this.setState(JSON.parse(localStorage.getItem("user_data")));
    }
  }
    render() {
      console.log(this.state.isLoggedIn);
      let layout;
      if (this.state.designation === "manager") {
        layout = <ManagerLayout 
                    name={ this.state.name } 
                    onLogout={ this.onLogout } 
                    uuid={this.state.uuid } 
                    email={ this.state.email }
                    address={ this.state.address }
                    aboutMe={ this.state.aboutMe }
                    age={ this.state.age }
                  />
      } else {
        layout = <TraineeLayout 
                    name={ this.state.name } 
                    onLogout={ this.onLogout } 
                    uuid={this.state.uuid } 
                    email={ this.state.email }
                    address={ this.state.address }
                    aboutMe={ this.state.aboutMe }
                    age={ this.state.age }
                  />
      }

      return (
          <Router>
            <Switch>
              <Route exact path="/">
                { this.state.isLoggedIn ? (
                  <LoggedInHomeNavbar name={ this.state.name } />
                ) : (
                  <HomeNavbar />
                )}
                <Home />
              </Route>
              <Route path="/login">
                <HomeNavbar />
                <Login onLogin={ this.onLogin } onLoginError={ this.onLoginError }/>
              </Route>
              <Route path="/signup">
                <HomeNavbar />
                <Signup onSignup={ this.onSignup } onSignupError={ this.onSignupError }/>
              </Route>
              <Route path="/user">
                { layout }    
              </Route>
              <Route render={ () => <Redirect to={{ pathname: "/"}} />} />
            </Switch>
          </Router>
      );
    }
}

export default App;
