import React, { useEffect, useState } from "react";
import {
    Card,
    CardDeck,
    Container,
    Row,
  } from "react-bootstrap";

export function Team(props) {
    const [ managerState, setManager ] = useState({});
    const [ teamState, setTeam ] = useState({});
    const { uuid, onBrandChange } = props;

    useEffect(() => {
        fetch('/https://train-backend-api.herokuapp.com/api/trainee/get_manager', {
            method: 'POST',
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': 'http://localhost:3000'
            },
            body: JSON.stringify({
                uuid: uuid
            })
        }).then(response => response.json()).then(json => {
            setManager({
                manager_name: json.manager_name,
                manager_uuid: json.manager_uuid,
                manager_email: json.manager_email
            });
        }).catch(error => {
            console.log(error);
        });

        fetch('/https://train-backend-api.herokuapp.com/api/trainee/get_team', {
            method: 'POST',
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': 'http://localhost:3000'
            },
            body: JSON.stringify({
                uuid: uuid
            })
        }).then(response => response.json()).then(json => {
            setTeam({
                team_names: json.team_names,
                team_emails: json.team_emails,
                team_uuids: json.team_uuids
            });
        }).catch(error => {
            console.log(error);
        });
    }, [uuid]);

    useEffect(() => {
        onBrandChange("My Team");
    });

    const names = Object.assign([], teamState.team_names);

    return (
        <React.Fragment>
            <Container>
                <div className="row justify-content-md-center">
                    <Row>
                        <Card bg="light" style={{ width: '36rem' }} className="mb-2">
                            <Card.Header>
                            <div className="row justify-content-md-center">
                                <h5 className="text-muted">Your Manager</h5>
                            </div>
                            </Card.Header>
                            <Card.Body className="row justify-content-md-center">
                                <img
                                    src={require("./../Assets/img/jake.jpg").default}
                                    alt="..."
                                    style={{width: "5rem", height: "5rem"}}
                                    className="rounded-circle"
                                ></img>
                            </Card.Body>
                            <div className="row justify-content-md-center">
                                <Card.Title>{ managerState.manager_name }</Card.Title>
                            </div>
                            <Card.Text className="row justify-content-md-center">
                                { managerState.manager_email }
                            </Card.Text>
                            <hr></hr>
                            <Card.Footer>
                                <p className="text-muted row justify-content-md-center">Contact your manager for any questions about your training.</p>
                            </Card.Footer>
                        </Card>
                    </Row>
                </div>
                <br />
                <div className="row justify-content-md-center"><h5 className="text-muted">Who You'll Be Working With</h5></div>
                <div className="row justify-content-md-center">
                    <hr></hr>
                    <Row>
                        <CardDeck>
                            { names.map((name, idx) =>
                                <Card key={idx} bg="light" style={{ width: '64rem' }}>
                                    <div className="row justify-content-md-center">
                                        <img 
                                            src={require("../Assets/img/" + names[idx].substr(0, names[idx].indexOf(" ")).toLowerCase() + ".jpg").default}
                                            alt="..."
                                            style={{height: "5rem", width: "5rem", marginTop: "1rem"}}
                                            className="rounded-circle"
                                        ></img>
                                    </div>
                                    <div className="row justify-content-md-center">
                                        <Card.Title style={{marginTop: "1rem"}}>{ name }</Card.Title>
                                    </div>
                                    <div className="row justify-content-md-center">
                                        <Card.Text style={{marginTop: "2rem"}}>{ teamState.team_emails[idx] }</Card.Text>
                                    </div>
                                    <hr></hr>
                                    <Card.Footer></Card.Footer>
                                </Card>
                            )}
                        </CardDeck>
                    </Row>
                </div>
            </Container>
        </React.Fragment>
    )
}